  .pben-circle__orbit--outside {
    inset: -1.5rem;
  }
}
.pben-circle__sphere {
  position: absolute;
  height: calc(100% + 15rem);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation: orbitSphere 10s linear infinite;
  animation-direction: reverse;
}
@media (max-width: 1024px) {
  .pben-circle__sphere {
    height: calc(100% + 5.5rem);
