  .pben-circle__sphere::before {
    width: 1.5rem;
    height: 1.5rem;
  }
}
.pben-circle__sphere:nth-of-type(2) {
  height: calc(100% + 8rem);
  animation-direction: unset;
}
@media (max-width: 1024px) {
  .pben-circle__sphere:nth-of-type(2) {
    height: calc(100% + 3.5rem);
  }
}
.pben-circle__sphere:nth-of-type(2)::before {
  width: 2rem;
  height: 2rem;
}
@media (max-width: 1024px) {
  .pben-circle__sphere:nth-of-type(2)::before {
    width: 0.6rem;
    height: 0.6rem;
  }
}
.pben-circle__sphere:nth-of-type(3) {
  height: calc(100% + 13rem);
  animation-delay: 0.3s;
}
@media (max-width: 1024px) {
  .pben-circle__sphere:nth-of-type(3) {
    height: calc(100% + 4.5rem);
