  height: 1.2rem;
}
@media (max-width: 1024px) {
  .pben-circle__sphere:nth-of-type(3)::before {
    width: 0.6rem;
    height: 0.6rem;
  }
}

@keyframes orbitInside {
  from {
    transform: rotateZ(0);
  }
  to {
