    border: 0.01rem solid #000;
    background: #fff;
  }
}
.eco-block table td {
  border: 0.05rem solid #000;
  padding: 0.7rem;
  padding-bottom: 1.2rem;
  font-weight: 300;
  font-size: 2rem;
  color: #141414;
  width: 8%;
  vertical-align: top;
}
@media (max-width: 1024px) {
  .eco-block table td {
    font-size: 0.6rem;
    padding: 0.3rem;
    border: 0.01rem solid #000;
  }
}
.eco-block table td:nth-child(1) {
  width: 16%;
}
.eco-block table td:last-child {
  width: 11.7%;
}